<template>
    <div>
        <div style="background: #FFF;padding: 2vh 2vw;">
            <div>
                <div style="text-align: center;color: #1C2023;font-size: 24px">{{jiezhuan_year}}年{{jiezhuan_month}}期 期末结转</div>

                <div style="font-size: 24px;color: #1C2023;margin: 3vh 0">
                    第二步 结转损益
                </div>
                <div>
                    <pingzheng :yaoxinxi="yaoxinxi" @geixinxi="geixinxi" type="jiezhuan"></pingzheng>

                    <div style="display: flex;justify-content: center;margin: 15vh">
                        <el-button type="warning" @click="$router.push({path:'/home/qimojiezhuan/one'})">上一步</el-button>
                        <el-button type="primary" style="margin-left: 3vw" @click="yaoxinxi=true">结账</el-button>
                        <el-button type="info" style="margin-left: 3vw" @click="$router.push({path:'/home'})">返回</el-button>
                    </div>

                </div>
            </div>


        </div>

    </div>
</template>

<script>
    import mix from "@/mixins/mixins"
    import pingzheng from "@/components/yuchunhui/add_pingzheng"

    export default {
        name: "one",
        components:{
            pingzheng
        },
        mixins:[mix],
        data(){
            return{
                yaoxinxi:false,

                form:{
                    search:'',
                },
                user:{
                    tableData: [{name:'',zhaiyao:'',jie:"",dai:"",jie_show:false,dai_show:false,index:0},
                        {name:'',zhaiyao:'',jie:"",dai:"",jie_show:false,dai_show:false,index:1}],
                }
            }
        },
        mounted(){
            this.getStorage()
            this.get_jiezhuan_date()
        },
        methods:{
            geixinxi(x,y){
                this.user = x
                this.ok()
            },

            ok(formName){
                let abstract = ''
                let debitMoney = ''
                let creditMoney = ''
                let name = ''
                let code = ''
                this.user.tableData.forEach(item=>{
                    abstract += item.zhaiyao + ','
                    if(item.jie == ''){
                        debitMoney+= 0+','
                    }else{
                        debitMoney+= item.jie+','
                    }

                    if(item.dai == ''){
                        creditMoney+= 0+','
                    }else{
                        creditMoney+= item.dai+','
                    }

                    // 处理有二级只要二级的分类
                    if(item.name.split(",")[0].includes("-")){
                        name += item.name.split(',')[0].split("-")[1]+','
                    }else{
                        name += item.name.split(',')[0]+','
                    }
                    code += item.name.split(',')[1]+','


                })
                this._ajax('userHomePage/addJieZhuanSunYi',{
                    uuid:this.uuid,
                    workAccountId:this.accountId,
                    time:this.jiezhuan_date + '-' + (Array(2).join(0) + new Date().getDate()).slice(-2),
                    number:this.form.search,
                    name,
                    code,
                    creditMoney,
                    debitMoney,
                    abstract
                }, msg => {
                    this._message(1,"结账成功")
                    this.$router.push({path:'/home'})
                })
            },

        }
    }
</script>

<style scoped>
    .el-table .lan {
        background: rgba(68,160,255,0.05);
    }

    .title{
        font-size: 18px;
        color: #40454B;
    }
    .content{
        font-size: 36px;
        color: #212542;
    }
</style>


